let isActive = false;

export default class KeycontrolSettings {
  static displayMenu() {
    const menu = document.querySelector('.keycontrol-settings-menu');
    if (isActive) {
      isActive = false;
      menu.animate(
        [
          { top: '0' },
          { top: '-100%' },
        ],
        { duration: 500 },
      );
      menu.style.top = '-100%';
    } else {
      isActive = true;
      menu.animate(
        [
          { top: '-100%' },
          { top: '0' },
        ],
        { duration: 500 },
      );
      menu.style.top = '0';
    }
  }
}
