import getPixelPosition from '../utils/GetPixelPosition';
import { canvasData } from '../utils/CanvasData';

let startX = null;
let startY = null;
let mouseIsDown = false;
let currentContext = null;

export default class Rectangle {
  static draw() {
    const position = getPixelPosition(window.event);
    const posX = position[0] + canvasData.pixelSize / 2;
    const posY = position[1] + canvasData.pixelSize / 2;
    let color = canvasData.primaryColor;

    const leftClick = 0;
    if (canvasData.mouseButton !== leftClick) {
      color = canvasData.secondaryColor;
    }
    currentContext.strokeStyle = color;

    const squareWidth = posX - startX;
    const squareHeight = posY - startY;
    currentContext.lineWidth = canvasData.pixelSize;

    currentContext.strokeRect(startX, startY, squareWidth, squareHeight);
  }

  static mouseDown() {
    const position = getPixelPosition(window.event);
    const posX = position[0];
    const posY = position[1];
    startX = posX + canvasData.pixelSize / 2;
    startY = posY + canvasData.pixelSize / 2;
    currentContext = canvasData.secondContext;
    mouseIsDown = true;
  }

  static mouseUp() {
    if (mouseIsDown === false) return;
    currentContext = canvasData.mainContext;
    Rectangle.draw();
    mouseIsDown = false;
  }

  static move() {
    if (mouseIsDown) Rectangle.draw();
  }
}
