import { canvasData } from './CanvasData';
import clearCanvas from './ClearCanvas';
import getPixelPosition from './GetPixelPosition';

export default function selectPixel() {
  clearCanvas();
  const position = getPixelPosition(window.event);
  const posX = position[0];
  const posY = position[1];
  const textX = posX / canvasData.defaultPixelSize + 1;
  const textY = posY / canvasData.defaultPixelSize + 1;

  const displayPosition = document.querySelector('.position');
  canvasData.secondContext.strokeStyle = 'black';
  canvasData.secondContext.lineWidth = 1;
  canvasData.secondContext.strokeRect(posX, posY, canvasData.pixelSize, canvasData.pixelSize);
  displayPosition.innerHTML = `${textX} / ${textY}`;
}
