import framesData from './FramesData';
import { canvasData } from '../utils/CanvasData';

const $ = require('jquery');
const _ = require('lodash');

export default class Frame {
  static frameTemplate(uniqId, number) {
    return $('<div>', {
      class: 'frame',
      'data-frameid': uniqId,
      append: ($('<button>', { class: 'frame-btn frame-btn-number', 'data-prop': 'choice', text: number }))
        .add($('<button>', { class: 'frame-btn frame-btn-delete frame-btn-hidden', 'data-prop': 'delete', title: 'Delete frame' }))
        .add($('<button>', { class: 'frame-btn frame-btn-move frame-btn-hidden', 'data-prop': 'move', title: 'Swap frame with another' }))
        .add($('<button>', { class: 'frame-btn frame-btn-copy frame-btn-hidden', 'data-prop': 'copy', title: 'Copy Frame' }))
        .add($('<canvas>', { class: 'frame-canvas' })),
    });
  }

  static generateFrame(id = null, number = framesData.nextFrameOrder, imageData = null) {
    const frameOrder = +number;
    framesData.nextFrameOrder = frameOrder + 1;
    let uniqId = id;

    if (_.isNull(uniqId)) {
      uniqId = `id${Math.random().toString(36).substr(2, 4)}`;
    }

    const frameTemp = Frame.frameTemplate(uniqId, number);
    const parent = document.querySelector('.frames');
    $(parent).append(frameTemp);

    const canvas = frameTemp.children().last();
    const domCanvas = canvas[0];

    const frameCanvasSize = framesData.frameSize;
    domCanvas.height = frameCanvasSize;
    domCanvas.width = frameCanvasSize;

    framesData[uniqId] = {
      frameOrder: number,
      frameImageData: imageData,
    };

    framesData.currentActiveFrame = uniqId;

    const frame = frameTemp[0];

    Frame.drawImg(domCanvas, imageData);

    return frame;
  }

  static drawImg(canvas, imageData) {
    if (_.isNull(imageData)) {
      canvasData.mainContext.clearRect(0, 0, canvasData.canvasSize, canvasData.canvasSize);
      return;
    }

    const frameCanvasContext = canvas.getContext('2d');
    const img = new Image();
    img.src = imageData;

    Promise.resolve(img.onload = () => {
      frameCanvasContext.drawImage(img, 0, 0);
      canvasData.mainContext.clearRect(0, 0, canvasData.canvasSize, canvasData.canvasSize);
      canvasData.mainContext.drawImage(canvas, 0, 0);
    });
  }
}
