import getPixelPosition from '../utils/GetPixelPosition';
import { canvasData } from '../utils/CanvasData';

let mouseIsDown = false;

export default class Eraser {
  static click() {
    const position = getPixelPosition(window.event);
    const posX = position[0];
    const posY = position[1];

    canvasData.mainContext.clearRect(posX, posY, canvasData.pixelSize, canvasData.pixelSize);
  }

  static mouseDown() {
    mouseIsDown = true;
  }

  static mouseUp() {
    mouseIsDown = false;
  }

  static move() {
    if (mouseIsDown) Eraser.click();
  }
}
