import { canvasData, loadDefault } from './CanvasData';

const _ = require('lodash');

export default function loadFromLocal() {
  const localData = localStorage.getItem('piskel');
  if (_.isNull(localData)) {
    loadDefault();
  } else {
    const data = JSON.parse(localData);
    Object.keys(data).forEach((key) => {
      if (!_.isObject(data[key])) canvasData[key] = data[key];
    });
  }
}
