import getPixelPosition from '../utils/GetPixelPosition';
import { canvasData } from '../utils/CanvasData';

const noColor = 'rgba(0,0,0,0)';

export default class Eyedropper {
  static click() {
    const position = getPixelPosition(window.event);
    const posX = position[0];
    const posY = position[1];

    const color = Eyedropper.getColor(posX, posY);
    if (color === noColor) return;

    const leftClick = 0;

    const primaryColor = document.querySelector('.primary-color-wrapper');
    const secondaryColor = document.querySelector('.secondary-color-wrapper');

    if (canvasData.mouseButton !== leftClick) {
      canvasData.secondaryColor = color;
      secondaryColor.style.backgroundColor = color;
    } else {
      canvasData.primaryColor = color;
      primaryColor.style.backgroundColor = color;
    }
  }

  static getColor(posX, posY) {
    const imageData = canvasData.mainContext.getImageData(posX, posY, 1, 1);
    const red = imageData.data[0];
    const green = imageData.data[1];
    const blue = imageData.data[2];
    const alpha = imageData.data[3];
    const color = `rgba(${red},${green},${blue},${alpha})`;
    return color;
  }
}
