import Pen from './Pen';
import RainbowPen from './RainbowPen';
import Eraser from './Eraser';
import Bucket from './Bucket';
import Eyedropper from './Eyedropper';
import Stroke from './Stroke';
import Rectangle from './Rectangle';
import Lightness from './Lightness';
import MirrorPen from './MirrorPen';
import KeycontrolSettings from './KeycontrolSettings';
import FillSamePixels from './FillSamePixels';
import Circle from './Circle';
import initColorSelect from './SelectColor';
import selectPixel from '../utils/SelectPixel';
import initPixelSizeTool, { showPixelSizeMenu } from './PixelSize';
import { canvasData } from '../utils/CanvasData';
import FramesController from '../frames/FramesController';

const _ = require('lodash');

let currentTool = null;

const buttons = {};

export default class ToolsController {
  static init() {
    const tools = document.querySelector('.tools');
    tools.addEventListener('click', ToolsController.activateTool);

    buttons.eyedropperBtn = document.querySelector('.btn-eyedropper');
    buttons.penBtn = document.querySelector('.btn-pen');
    buttons.bucketBtn = document.querySelector('.btn-bucket');
    buttons.eraserBtn = document.querySelector('.btn-eraser');
    buttons.strokeBtn = document.querySelector('.btn-stroke');
    buttons.rectangleBtn = document.querySelector('.btn-rectangle');
    buttons.sizeBtn = document.querySelector('.btn-size');
    buttons.lightnessBtn = document.querySelector('.btn-lightness');
    buttons.mirrorPenBtn = document.querySelector('.btn-mirror-pen');
    buttons.fillSameBtn = document.querySelector('.btn-fill-same');
    buttons.circleBtn = document.querySelector('.btn-circle');
    buttons.rainbowPenBtn = document.querySelector('.btn-rainbow-pen');
    buttons.keyControl = document.querySelector('.btn-keycontrol');
    buttons.keyControl.addEventListener('click', KeycontrolSettings.displayMenu);

    canvasData.secondCanvas.addEventListener('mousemove', selectPixel);

    canvasData.secondCanvas.addEventListener('click', ToolsController.click);
    canvasData.secondCanvas.addEventListener('mousedown', ToolsController.mouseDown);
    canvasData.secondCanvas.addEventListener('mousemove', ToolsController.move);
    canvasData.secondCanvas.addEventListener('mouseup', ToolsController.mouseUp);
    canvasData.secondCanvas.addEventListener('contextmenu', ToolsController.click);

    document.addEventListener('mouseup', ToolsController.mouseUp);
    document.addEventListener('click', ToolsController.saveData);

    window.addEventListener('keydown', ToolsController.keyControl);

    initColorSelect();
    initPixelSizeTool();
  }

  static keyControl({ code }) {
    const keyCode = code;
    switch (keyCode) {
      case ('KeyP'):
        currentTool = Pen;
        ToolsController.makeToolActive(buttons.penBtn);
        break;

      case ('KeyC'):
        currentTool = Circle;
        ToolsController.makeToolActive(buttons.circleBtn);
        break;

      case ('KeyB'):
        currentTool = Bucket;
        ToolsController.makeToolActive(buttons.bucketBtn);
        break;

      case ('KeyL'):
        currentTool = Lightness;
        ToolsController.makeToolActive(buttons.lightnessBtn);
        break;

      case ('KeyY'):
        currentTool = Eyedropper;
        ToolsController.makeToolActive(buttons.eyedropperBtn);
        break;

      case ('KeyE'):
        currentTool = Eraser;
        ToolsController.makeToolActive(buttons.eraserBtn);
        break;

      case ('KeyR'):
        currentTool = Rectangle;
        ToolsController.makeToolActive(buttons.rectangleBtn);
        break;

      case ('KeyS'):
        currentTool = Stroke;
        ToolsController.makeToolActive(buttons.strokeBtn);
        break;

      case ('KeyM'):
        currentTool = MirrorPen;
        ToolsController.makeToolActive(buttons.mirrorPenBtn);
        break;

      case ('KeyF'):
        currentTool = FillSamePixels;
        ToolsController.makeToolActive(buttons.fillSameBtn);
        break;

      case ('KeyK'):
        KeycontrolSettings.displayMenu();
        break;

      default:
        break;
    }
  }

  static activateTool({ target }) {
    switch (target) {
      case buttons.penBtn:
        currentTool = Pen;
        ToolsController.makeToolActive(buttons.penBtn);
        break;

      case buttons.circleBtn:
        currentTool = Circle;
        ToolsController.makeToolActive(buttons.circleBtn);
        break;

      case buttons.bucketBtn:
        currentTool = Bucket;
        ToolsController.makeToolActive(buttons.bucketBtn);
        break;

      case buttons.eyedropperBtn:
        currentTool = Eyedropper;
        ToolsController.makeToolActive(buttons.eyedropperBtn);
        break;

      case buttons.eraserBtn:
        currentTool = Eraser;
        ToolsController.makeToolActive(buttons.eraserBtn);
        break;

      case buttons.strokeBtn:
        currentTool = Stroke;
        ToolsController.makeToolActive(buttons.strokeBtn);
        break;

      case buttons.rectangleBtn:
        currentTool = Rectangle;
        ToolsController.makeToolActive(buttons.rectangleBtn);
        break;

      case buttons.fillSameBtn:
        currentTool = FillSamePixels;
        ToolsController.makeToolActive(buttons.fillSameBtn);
        break;

      case buttons.lightnessBtn:
        currentTool = Lightness;
        ToolsController.makeToolActive(buttons.lightnessBtn);
        break;

      case buttons.mirrorPenBtn:
        currentTool = MirrorPen;
        ToolsController.makeToolActive(buttons.mirrorPenBtn);
        break;

      case buttons.rainbowPenBtn:
        currentTool = RainbowPen;
        ToolsController.makeToolActive(buttons.rainbowPenBtn);
        break;

      case buttons.sizeBtn:
        showPixelSizeMenu();
        break;

      default:
        break;
    }
  }

  static makeToolActive(btn) {
    const activeBtn = document.querySelector('.tool-btn-active');
    if (!_.isNull(activeBtn)) activeBtn.classList.toggle('tool-btn-active');
    btn.classList.toggle('tool-btn-active');
  }

  static click(event) {
    event.preventDefault();
    if (_.isNull(currentTool) || _.isUndefined(currentTool.click)) return;
    currentTool.click();
    FramesController.updateFrameState();
  }

  static move() {
    if (_.isNull(currentTool) || _.isUndefined(currentTool.move)) return;
    currentTool.move();
  }

  static mouseDown(event) {
    canvasData.mouseButton = event.button;
    if (_.isNull(currentTool) || _.isUndefined(currentTool.mouseDown)) return;
    currentTool.mouseDown();
  }

  static mouseUp() {
    if (_.isNull(currentTool) || _.isUndefined(currentTool.mouseUp)) return;
    currentTool.mouseUp();
    FramesController.updateFrameState();
  }
}
