import getPixelPosition from '../utils/GetPixelPosition';
import { canvasData } from '../utils/CanvasData';

let fillColor = null;
let targetColor = null;

export default class FillSamePixels {
  static click() {
    const position = getPixelPosition(window.event);
    const posX = position[0];
    const posY = position[1];

    targetColor = FillSamePixels.getColor(posX, posY);

    fillColor = canvasData.primaryColor;
    const leftClick = 0;
    if (canvasData.mouseButton !== leftClick) {
      fillColor = canvasData.secondaryColor;
    }

    FillSamePixels.fillPixel(posX, posY);

    if (FillSamePixels.colorIsSame(posX, posY)) return;

    FillSamePixels.fillOthers();
  }

  static colorIsSame(posX, posY) {
    const color = FillSamePixels.getColor(posX, posY);
    return (color === targetColor);
  }

  static fillPixel(posX, posY) {
    canvasData.mainContext.fillStyle = fillColor;
    const pixelSize = canvasData.defaultPixelSize;
    canvasData.mainContext.fillRect(posX, posY, pixelSize, pixelSize);
  }

  static getColor(posX, posY) {
    const imageData = canvasData.mainContext.getImageData(posX, posY, 1, 1);
    const red = imageData.data[0];
    const green = imageData.data[1];
    const blue = imageData.data[2];
    const alpha = imageData.data[3];
    const color = `rgba(${red},${green},${blue},${alpha})`;
    return color;
  }

  static fillOthers() {
    for (let posX = 0; posX < canvasData.canvasSize; posX += canvasData.defaultPixelSize) {
      for (let posY = 0; posY < canvasData.canvasSize; posY += canvasData.defaultPixelSize) {
        if (FillSamePixels.colorIsSame(posX, posY)) {
          FillSamePixels.fillPixel(posX, posY);
        }
      }
    }
  }
}
