import getPixelPosition from '../utils/GetPixelPosition';
import { canvasData } from '../utils/CanvasData';

let startX = null;
let startY = null;
let mouseIsDown = false;
let currentContext = null;

export default class Stroke {
  static mouseDown() {
    const position = getPixelPosition(window.event);
    const posX = position[0];
    const posY = position[1];
    startX = posX;
    startY = posY;
    currentContext = canvasData.secondContext;
    mouseIsDown = true;
  }

  static mouseUp() {
    if (mouseIsDown === false) return;
    currentContext = canvasData.mainContext;
    Stroke.drawLine();
    mouseIsDown = false;
  }

  static move() {
    if (mouseIsDown) Stroke.drawLine();
  }

  static drawLine() {
    const position = getPixelPosition(window.event);
    const endX = position[0];
    const endY = position[1];
    let currentX = startX;
    let currentY = startY;
    const deltaX = Math.abs(endX - startX);
    const deltaY = Math.abs(endY - startY);
    let delta = deltaX - deltaY;
    const signX = startX < endX ? canvasData.defaultPixelSize : -canvasData.defaultPixelSize;
    const signY = startY < endY ? canvasData.defaultPixelSize : -canvasData.defaultPixelSize;

    Stroke.fillPixel(endX, endY);
    while (currentX !== endX || currentY !== endY) {
      Stroke.fillPixel(currentX, currentY);

      const doubleDelta = delta * 2;

      if (doubleDelta > -deltaY) {
        delta -= deltaY;
        currentX += signX;
      }

      if (doubleDelta < deltaX) {
        delta += deltaX;
        currentY += signY;
      }
    }
  }

  static fillPixel(posX, posY) {
    let color = canvasData.primaryColor;
    const leftClick = 0;
    if (canvasData.mouseButton !== leftClick) {
      color = canvasData.secondaryColor;
    }

    currentContext.fillStyle = color;
    currentContext.fillRect(posX, posY, canvasData.pixelSize, canvasData.pixelSize);
  }
}
