export const canvasData = {};

export function initCanvasData() {
  const mainCanvas = document.querySelector('.canvas-main');
  const secondCanvas = document.querySelector('.canvas-layer');
  const defaultCanvasSize = 1280;
  canvasData.mainCanvas = mainCanvas;
  canvasData.mainContext = mainCanvas.getContext('2d');
  canvasData.secondCanvas = secondCanvas;
  canvasData.secondContext = secondCanvas.getContext('2d');
  mainCanvas.width = defaultCanvasSize;
  mainCanvas.height = defaultCanvasSize;
  secondCanvas.width = defaultCanvasSize;
  secondCanvas.height = defaultCanvasSize;
}

export function loadDefault() {
  canvasData.pixelsCount = 32;
  canvasData.canvasSize = 1280;
  canvasData.defaultPixelSize = canvasData.canvasSize / canvasData.pixelsCount;
  canvasData.pixelSize = canvasData.canvasSize / canvasData.pixelsCount;

  canvasData.primaryColor = '#ff0000';
  canvasData.secondaryColor = '#0000ff';

  canvasData.mouseButton = null;
}
