import { canvasData } from '../utils/CanvasData';

const colorSelectData = {};

function changeColor({ target }) {
  const color = target.value;
  if (target.classList.contains('tools-primary-color')) {
    canvasData.primaryColor = color;
    colorSelectData.primaryColor.style.backgroundColor = color;
  } else {
    canvasData.secondaryColor = color;
    colorSelectData.secondaryColor.style.backgroundColor = color;
  }
}

function primaryClick() {
  colorSelectData.primaryColorSelector.click();
}

function secondaryClick() {
  colorSelectData.secondaryColorSelector.click();
}

export default function initColorSelect() {
  colorSelectData.primaryColor = document.querySelector('.primary-color-wrapper');
  colorSelectData.primaryColorSelector = document.querySelector('.tools-primary-color');
  colorSelectData.secondaryColor = document.querySelector('.secondary-color-wrapper');
  colorSelectData.secondaryColorSelector = document.querySelector('.tools-secondary-color');
  colorSelectData.primaryColor.addEventListener('click', primaryClick);
  colorSelectData.secondaryColor.addEventListener('click', secondaryClick);
  colorSelectData.primaryColorSelector.addEventListener('change', changeColor);
  colorSelectData.secondaryColorSelector.addEventListener('change', changeColor);

  colorSelectData.primaryColor.style.backgroundColor = canvasData.primaryColor;
  colorSelectData.secondaryColor.style.backgroundColor = canvasData.secondaryColor;
}
