import getPixelPosition from '../utils/GetPixelPosition';
import { canvasData } from '../utils/CanvasData';

let mouseIsDown = false;

export default class RainbowPen {
  static click() {
    const position = getPixelPosition(window.event);
    const posX = position[0];
    const posY = position[1];
    const color = RainbowPen.generateColor();
    canvasData.mainContext.fillStyle = color;
    canvasData.mainContext.fillRect(posX, posY, canvasData.pixelSize, canvasData.pixelSize);
  }

  static mouseDown() {
    mouseIsDown = true;
  }

  static mouseUp() {
    mouseIsDown = false;
  }

  static move() {
    if (mouseIsDown) RainbowPen.click();
  }

  static generateColor() {
    const red = Math.floor(Math.random(1) * 255);
    const green = Math.floor(Math.random(1) * 255);
    const blue = Math.floor(Math.random(1) * 255);
    const color = `rgba(${red}, ${green}, ${blue}, 255)`;
    return color;
  }
}
