import { canvasData } from './CanvasData';
import framesData from '../frames/FramesData';
import saveToLocal from './saveToLocalStorage';
import framesInit from '../frames/FramesInit';

const _ = require('lodash');

export default function updateData(data) {
  Object.keys(data).forEach((key) => {
    if (!_.isObject(data[key])) canvasData[key] = data[key];
  });

  Object.keys(framesData).forEach((key) => {
    framesData[key] = null;
  });

  const frames = document.querySelector('.frames');
  frames.innerHTML = '';
  const loadedData = JSON.parse(canvasData.framesData);
  framesInit.loadState(loadedData);

  saveToLocal();
}
