import getPixelPosition from '../utils/GetPixelPosition';
import { canvasData } from '../utils/CanvasData';

let mouseIsDown = false;

export default class MirrorPen {
  static click() {
    const position = getPixelPosition(window.event);
    const posX = position[0];
    const posY = position[1];
    let color = canvasData.primaryColor;
    const leftClick = 0;
    if (canvasData.mouseButton !== leftClick) {
      color = canvasData.secondaryColor;
    }
    canvasData.mainContext.fillStyle = color;
    canvasData.mainContext.fillRect(posX, posY, canvasData.pixelSize, canvasData.pixelSize);
    const secondPosX = (canvasData.canvasSize - posX - canvasData.pixelSize);
    canvasData.mainContext.fillRect(secondPosX, posY, canvasData.pixelSize, canvasData.pixelSize);
  }

  static mouseDown() {
    mouseIsDown = true;
  }

  static mouseUp() {
    mouseIsDown = false;
  }

  static move() {
    if (mouseIsDown) MirrorPen.click();
  }
}
