import { canvasData } from '../utils/CanvasData';

function changePixelSize({ target }) {
  const displaySize = document.querySelector('.btn-size');
  if (target.classList.contains('size-menu-btn')) {
    const pixelSizeMenu = document.querySelector('.tools-btn-size-menu');
    pixelSizeMenu.style.display = 'none';
    const multiple = +target.dataset.size;
    canvasData.pixelSize = canvasData.defaultPixelSize * multiple;
    displaySize.innerHTML = `Size: ${multiple}`;
  }
}

export default function initPixelSizeTool() {
  const pixelSizeMenu = document.querySelector('.tools-btn-size-menu');
  pixelSizeMenu.addEventListener('click', changePixelSize);
}

export function showPixelSizeMenu() {
  const pixelSizeMenu = document.querySelector('.tools-btn-size-menu');
  pixelSizeMenu.style.display = 'flex';
}
