import { canvasData } from './CanvasData';

export default function getPixelPosition({ offsetX, offsetY }) {
  const currentCanvasSize = canvasData.mainCanvas.offsetWidth;
  const pixelX = Math.floor(offsetX / currentCanvasSize * canvasData.pixelsCount);
  const pixelY = Math.floor(offsetY / currentCanvasSize * canvasData.pixelsCount);
  const posX = pixelX * canvasData.defaultPixelSize;
  const posY = pixelY * canvasData.defaultPixelSize;
  return [posX, posY];
}
