import getPixelPosition from '../utils/GetPixelPosition';
import { canvasData } from '../utils/CanvasData';

let startX = null;
let startY = null;
let mouseIsDown = false;
let currentContext = null;

export default class Circle {
  static mouseDown() {
    const position = getPixelPosition(window.event);
    const posX = position[0];
    const posY = position[1];
    startX = posX;
    startY = posY;
    currentContext = canvasData.secondContext;
    mouseIsDown = true;
  }

  static mouseUp() {
    if (mouseIsDown === false) return;
    currentContext = canvasData.mainContext;
    Circle.drawCircle();
    mouseIsDown = false;
  }

  static move() {
    if (mouseIsDown) Circle.drawCircle();
  }

  static drawCircle() {
    const position = getPixelPosition(window.event);
    const endX = position[0];
    const endY = position[1];

    const deltaX = Math.abs(endX - startX);
    const deltaY = Math.abs(endY - startY);

    const pixelSize = canvasData.defaultPixelSize;

    const centerX = Math.abs((endX + startX) / 2);
    const centerY = Math.abs((endY + startY) / 2);

    const radius = Math.sqrt(((deltaX ** 2) + (deltaY ** 2)) / 2);

    for (let degree = 0; degree <= 360; degree += 1) {
      const angle = (degree * Math.PI / 180);

      const currentX = radius * Math.cos(angle);
      const currentY = radius * Math.sin(angle);

      const posX = Math.floor(currentX / pixelSize) * pixelSize + centerX;
      const posY = Math.floor(currentY / pixelSize) * pixelSize + centerY;

      Circle.fillPixel(posX, posY);
    }
  }

  static fillPixel(posX, posY) {
    let color = canvasData.primaryColor;
    const leftClick = 0;
    if (canvasData.mouseButton !== leftClick) {
      color = canvasData.secondaryColor;
    }
    currentContext.fillStyle = color;
    currentContext.fillRect(posX, posY, canvasData.pixelSize, canvasData.pixelSize);
  }
}
