import getPixelPosition from '../utils/GetPixelPosition';
import { canvasData } from '../utils/CanvasData';

export default class Lightness {
  static click() {
    const position = getPixelPosition(window.event);
    const posX = position[0];
    const posY = position[1];

    let lightDiff = 0.05;

    const leftClick = 0;
    if (canvasData.mouseButton !== leftClick) {
      lightDiff = -0.05;
    }

    const imageData = canvasData.mainContext.getImageData(posX, posY, 1, 1);

    let red = imageData.data[0] / 255;
    let green = imageData.data[1] / 255;
    let blue = imageData.data[2] / 255;

    const channelMin = Math.min(red, green, blue);
    const channelMax = Math.max(red, green, blue);
    const delta = channelMax - channelMin;
    let hue = 0;
    let saturation = 0;
    let lightness = 0;

    if (delta === 0) {
      hue = 0;
    } else if (channelMax === red) {
      hue = ((green - blue) / delta) % 6;
    } else if (channelMax === green) {
      hue = (blue - red) / delta + 2;
    } else {
      hue = (red - green) / delta + 4;
    }

    hue = Math.round(hue * 60);

    if (hue < 0) hue += 360;

    lightness = (channelMax + channelMin) / 2;

    saturation = delta === 0 ? 0 : delta / (1 - Math.abs(2 * lightness - 1));

    lightness += lightDiff;

    if (lightness < 0) lightness = 0;
    if (lightness > 100) lightness = 100;

    const colorIntensity = (1 - Math.abs(2 * lightness - 1)) * saturation;
    const x = colorIntensity * (1 - Math.abs((hue / 60) % 2) - 1);
    const matchLightness = lightness - colorIntensity / 2;

    if (hue >= 0 && hue < 60) {
      red = colorIntensity;
      green = x;
      blue = 0;
    } else if (hue >= 60 && hue < 120) {
      red = x;
      green = colorIntensity;
      blue = 0;
    } else if (hue >= 120 && hue < 180) {
      red = 0;
      green = colorIntensity;
      blue = x;
    } else if (hue >= 180 && hue < 240) {
      red = 0;
      green = x;
      blue = colorIntensity;
    } else if (hue >= 240 && hue < 300) {
      red = x;
      green = 0;
      blue = colorIntensity;
    } else if (hue >= 300 && hue < 360) {
      red = colorIntensity;
      green = 0;
      blue = x;
    }

    red = Math.round((red + matchLightness) * 255);
    green = Math.round((green + matchLightness) * 255);
    blue = Math.round((blue + matchLightness) * 255);

    const color = `rgba(${red}, ${green}, ${blue}, 255)`;
    canvasData.mainContext.fillStyle = color;
    canvasData.mainContext.fillRect(posX, posY, canvasData.pixelSize, canvasData.pixelSize);
  }
}
