import framesData from './FramesData';
import { canvasData } from '../utils/CanvasData';
import Frame from './Frame';
import saveToLocalStorage from '../utils/saveToLocalStorage';

const $ = require('jquery');
const _ = require('lodash');

let movedFrame = null;
let activeBtn = null;
let startPosition = null;

export default class FramesController {
  static checkBtn({ target, currentTarget }) {
    if (!target.classList.contains('frame-btn')) return;
    const { prop } = target.dataset;
    let frameId = null;
    switch (prop) {
      case 'choice':
        frameId = currentTarget.dataset.frameid;
        FramesController.changeActiveFrame(frameId);
        FramesController.makeBtnActive(target);
        break;

      case 'copy':
        frameId = currentTarget.dataset.frameid;
        FramesController.copyFrame(frameId);
        break;

      case 'delete':
        frameId = currentTarget.dataset.frameid;
        FramesController.deleteFrame(frameId);
        break;

      default:
        break;
    }
  }

  static changeActiveFrame(id) {
    const frameId = id;
    const frame = document.querySelector(`[data-frameid=${frameId}]`);
    const frameCanvas = frame.querySelector('.frame-canvas');
    const btn = frame.querySelector('.frame-btn-number');

    framesData.currentActiveFrame = frameId;
    canvasData.mainContext.clearRect(0, 0, canvasData.canvasSize, canvasData.canvasSize);
    canvasData.mainContext.drawImage(frameCanvas, 0, 0);

    FramesController.makeBtnActive(btn);
    FramesController.updateFramesNumbers();

    Promise.resolve(FramesController.updateFrameState);
  }

  static copyFrame(id) {
    const parentId = id;
    const copyOrder = framesData.nextFrameOrder;
    const copyImgData = framesData[parentId].frameImageData;
    const uniqId = `id${Math.random().toString(36).substr(2, 4)}`;
    const frame = Frame.generateFrame(uniqId, copyOrder, copyImgData);

    FramesController.addEvents(frame);

    FramesController.changeActiveFrame(uniqId);
  }

  static deleteFrame(id) {
    const frameId = id;
    const frame = document.querySelector(`[data-frameid=${frameId}]`);

    if (frameId === framesData.currentActiveFrame) {
      canvasData.mainContext.clearRect(0, 0, canvasData.canvasSize, canvasData.canvasSize);
      const activeFrame = $(frame).prev() || $(frame).next();
      if ($(activeFrame).length > 0) {
        const { frameid } = $(activeFrame).data();
        FramesController.changeActiveFrame(frameid);
      }
    }

    $(frame).detach();
    framesData[frameId] = undefined;

    FramesController.updateFramesNumbers();

    FramesController.checkFramesCount();
  }

  static addFrame() {
    const frame = Frame.generateFrame(null, framesData.currentFramesNumber, null);
    const { frameid } = frame.dataset;

    FramesController.addEvents(frame);

    FramesController.changeActiveFrame(frameid);
  }

  static checkFramesCount() {
    const frames = document.querySelector('.frames');

    const framesCount = frames.childNodes.length;

    if (!framesCount) {
      const canvasSizeMenu = document.querySelector('.frame-size-menu');
      canvasSizeMenu.style.display = 'flex';
    }

    return framesCount;
  }

  static changeCanvasSize({ target }) {
    const { count } = target.dataset;
    canvasData.pixelsCount = count;
    canvasData.defaultPixelSize = canvasData.canvasSize / canvasData.pixelsCount;
    canvasData.pixelSize = canvasData.canvasSize / canvasData.pixelsCount;

    const canvasSizeMenu = document.querySelector('.frame-size-menu');
    canvasSizeMenu.style.display = 'none';

    FramesController.addFrame();
  }

  static moveFrame({ pageY, target }) {
    startPosition = pageY;
    const targetParent = $(target).parent();
    const frame = targetParent[0];
    movedFrame = frame;
    movedFrame.style.position = 'relative';
    movedFrame.style.zIndex = 2;
    document.addEventListener('mousemove', FramesController.move);
  }

  static move({ pageY }) {
    movedFrame.style.top = `${pageY - startPosition}px`;
  }

  static dropFrame({ pageX, pageY }) {
    if (_.isNull(movedFrame)) return;
    document.removeEventListener('mousemove', FramesController.move);

    movedFrame.style.position = 'none';
    movedFrame.style.top = 0;
    movedFrame.style.zIndex = 0;

    const dropZone = document.elementFromPoint(pageX, pageY);

    const target = $(dropZone).parent();

    const targetFrame = target[0];

    if (targetFrame === movedFrame || !targetFrame.classList.contains('frame')) {
      movedFrame = null;
      startPosition = null;
      return;
    }

    const deletedFrame = $(movedFrame).detach();
    const deletedOrderId = deletedFrame[0].dataset.frameid;
    const targetOrderId = target[0].dataset.frameid;
    const deletedOrder = framesData[deletedOrderId].frameOrder;
    const targetOrder = framesData[targetOrderId].frameOrder;

    if (deletedOrder > targetOrder) {
      $(target).before(deletedFrame);
    } else {
      $(target).after(deletedFrame);
    }

    framesData[deletedOrderId].frameOrder = targetOrder;
    framesData[targetOrderId].frameOrder = deletedOrder;

    movedFrame = null;
    startPosition = null;

    FramesController.updateFramesNumbers();
    saveToLocalStorage();
  }

  static makeBtnActive(target) {
    const activeClass = 'frame-btn-active';
    const targetBtn = target;

    if (!_.isNull(activeBtn)) activeBtn.classList.toggle(activeClass);
    targetBtn.classList.toggle(activeClass);
    activeBtn = targetBtn;
  }

  static updateFramesNumbers() {
    const frames = document.querySelectorAll('.frame');
    frames.forEach((elem, index) => {
      const frameNumber = index + 1;
      const frame = elem;
      const { frameid } = frame.dataset;
      frame.querySelector('.frame-btn-number').innerHTML = frameNumber;
      framesData[frameid].frameOrder = frameNumber;
      framesData.nextFrameOrder = frameNumber + 1;
    });
  }

  static updateFrameState() {
    const frameId = framesData.currentActiveFrame;
    const frame = document.querySelector(`[data-frameid=${frameId}]`);
    if (_.isNull(frame)) {
      saveToLocalStorage();
      return;
    }
    const frameCanvas = frame.querySelector('.frame-canvas');
    const canvasContext = frameCanvas.getContext('2d');
    canvasContext.clearRect(0, 0, framesData.frameSize, framesData.frameSize);
    canvasContext.drawImage(canvasData.mainCanvas, 0, 0);

    framesData[frameId].frameImageData = frameCanvas.toDataURL('image/png');

    saveToLocalStorage();
  }

  static addEvents(frame) {
    frame.addEventListener('click', FramesController.checkBtn);
    frame.addEventListener('mouseover', FramesController.displayButtons);
    frame.addEventListener('mouseout', FramesController.hideButtons);
    frame.addEventListener('mouseup', FramesController.dropFrame);
    const moveBtn = frame.querySelector('.frame-btn-move');
    moveBtn.addEventListener('mousedown', FramesController.moveFrame);
  }

  static displayButtons({ currentTarget }) {
    const displayBlock = 'block';
    const btnArr = currentTarget.querySelectorAll('.frame-btn-hidden');
    btnArr.forEach((btn) => {
      const targetBtn = btn;
      targetBtn.style.display = displayBlock;
    });
  }

  static hideButtons({ currentTarget }) {
    const displayNone = 'none';
    const btnArr = currentTarget.querySelectorAll('.frame-btn-hidden');
    btnArr.forEach((btn) => {
      const targetBtn = btn;
      targetBtn.style.display = displayNone;
    });
  }
}
