import framesData from './FramesData';
import Frame from './Frame';
import FramesController from './FramesController';
import { canvasData } from '../utils/CanvasData';

const _ = require('lodash');

export default class FramesInit {
  static init() {
    const dataFromStorage = canvasData.framesData;
    const addFrameBtn = document.querySelector('.frame-btn-add');
    addFrameBtn.addEventListener('click', FramesController.addFrame);

    const canvasSizeButtons = document.querySelectorAll('.frame-size-btn');
    canvasSizeButtons.forEach((btn) => {
      btn.addEventListener('click', FramesController.changeCanvasSize);
    });

    if (_.isUndefined(dataFromStorage)) {
      FramesController.checkFramesCount();
    } else {
      const loadedData = JSON.parse(dataFromStorage);
      FramesInit.loadState(loadedData);
    }

    window.addEventListener('keydown', FramesController.keyControl);
  }

  static loadState(loadedData) {
    const arr = [];
    Object.keys(loadedData).forEach((key) => {
      framesData[key] = loadedData[key];
      if (_.isObject(framesData[key])) {
        const frame = framesData[key];
        const index = frame.frameOrder;
        arr[index] = key;
      }
    });
    arr.forEach((elem) => {
      const frameId = elem;
      const frameData = framesData[frameId];
      const frame = Frame.generateFrame(elem, frameData.frameOrder, frameData.frameImageData);

      FramesController.addEvents(frame);

      const btn = frame.querySelector('.frame-btn-number');
      FramesController.makeBtnActive(btn);
    });
  }

  static keyControl({ code }) {
    const keyCode = code;
    switch (keyCode) {
      case ('KeyA'):
        FramesController.addFrame();
        break;
      default:
        break;
    }
  }
}
