import getPixelPosition from '../utils/GetPixelPosition';
import { canvasData } from '../utils/CanvasData';

const pixelStack = [];
const pixelSet = new Set();

let fillColor = null;
let targetColor = null;
let matchPixel = null;

export default class Bucket {
  static click() {
    const position = getPixelPosition(window.event);
    const posX = position[0];
    const posY = position[1];

    pixelStack.push([posX, posY]);
    pixelSet.add(`${posX}:${posY}`);

    targetColor = Bucket.getColor(posX, posY);

    fillColor = canvasData.primaryColor;
    const leftClick = 0;
    if (canvasData.mouseButton !== leftClick) {
      fillColor = canvasData.secondaryColor;
    }

    if (Bucket.colorIsSame(posX, posY)) return;

    Bucket.draw();
    pixelSet.clear();
  }

  static colorIsSame(posX, posY) {
    Bucket.fillPixel(posX, posY);
    const color = Bucket.getColor(posX, posY);
    return (color === targetColor);
  }

  static fillPixel(posX, posY) {
    canvasData.mainContext.fillStyle = fillColor;
    const pixelSize = canvasData.defaultPixelSize;
    canvasData.mainContext.fillRect(posX, posY, pixelSize, pixelSize);
  }

  static getColor(posX, posY) {
    const imageData = canvasData.mainContext.getImageData(posX, posY, 1, 1);
    const red = imageData.data[0];
    const green = imageData.data[1];
    const blue = imageData.data[2];
    const alpha = imageData.data[3];
    const color = `rgba(${red},${green},${blue},${alpha})`;
    return color;
  }

  static draw() {
    while (pixelStack.length > 0) {
      const currentPixel = pixelStack.pop();
      const posX = currentPixel[0];
      const posY = currentPixel[1];

      const posXIsOk = (posX >= 0 && posX < canvasData.canvasSize);
      const posYIsOk = (posY >= 0 && posY < canvasData.canvasSize);

      if (posXIsOk && posYIsOk) {
        Bucket.fillPixel(posX, posY);
        Bucket.addNextPixels(posX, posY);
      }
    }
  }

  static addNextPixels(x, y) {
    let posX = x;
    let posY = y - canvasData.defaultPixelSize;

    Bucket.checkPixel(posX, posY);
    if (matchPixel) pixelStack.push([posX, posY]);

    posY = y + canvasData.defaultPixelSize;
    Bucket.checkPixel(posX, posY);
    if (matchPixel) pixelStack.push([posX, posY]);

    posX = x - canvasData.defaultPixelSize;
    posY = y;
    Bucket.checkPixel(posX, posY);
    if (matchPixel) pixelStack.push([posX, posY]);

    posX = x + canvasData.defaultPixelSize;
    Bucket.checkPixel(posX, posY);
    if (matchPixel) pixelStack.push([posX, posY]);
  }

  static checkPixel(posX, posY) {
    const posXIsOk = (posX >= 0 && posX < canvasData.canvasSize);
    const posYIsOk = (posY >= 0 && posY < canvasData.canvasSize);

    if (!posXIsOk || !posYIsOk) {
      matchPixel = false;
      return;
    }

    if (pixelSet.has(`${posX}:${posY}`)) {
      matchPixel = false;
      return;
    }

    const color = Bucket.getColor(posX, posY);
    if (color !== targetColor) {
      matchPixel = false;
      return;
    }

    pixelSet.add(`${posX}:${posY}`);

    Bucket.fillPixel(posX, posY);

    matchPixel = true;
  }
}
