import { canvasData } from '../utils/CanvasData';
import updateData from '../utils/UpdateData';
import GIF from './gif';

const _ = require('lodash');

const animPlayerData = {};

let index = 0;
let canvasArr = [];
let delay = null;
let animateIsExist = false;

export default class AnimationPlayer {
  static init() {
    animPlayerData.animationPlayerCanvas = document.querySelector('.animation-player-canvas');
    animPlayerData.animationContext = animPlayerData.animationPlayerCanvas.getContext('2d');
    animPlayerData.chooseFps = document.querySelector('.animation-player-fps-range');
    animPlayerData.fpsText = document.querySelector('.animation-player-fps-text');
    animPlayerData.startBtn = document.querySelector('.btn-start-animation');
    animPlayerData.fullScreenBtn = document.querySelector('.btn-fullscreen');
    animPlayerData.animPlayer = document.querySelector('.animation-player');
    animPlayerData.animationPlayerCanvas.width = canvasData.canvasSize;
    animPlayerData.animationPlayerCanvas.height = canvasData.canvasSize;

    animPlayerData.chooseFps.addEventListener('input', AnimationPlayer.displayFps);

    animPlayerData.startBtn.addEventListener('click', AnimationPlayer.animationControl);
    animPlayerData.fullScreenBtn.addEventListener('click', AnimationPlayer.fullScreen);

    const saveAsPngBtn = document.querySelector('.export-tool-btn-png');
    saveAsPngBtn.addEventListener('click', AnimationPlayer.saveAsPng);

    const saveAsGifBtn = document.querySelector('.export-tool-btn-gif');
    saveAsGifBtn.addEventListener('click', AnimationPlayer.saveAsGif);

    const ownFormatBtn = document.querySelector('.export-tool-btn-wpcd');
    ownFormatBtn.addEventListener('click', AnimationPlayer.saveAsWPCD);

    const importWPCDBtn = document.querySelector('.import-tool-btn-wpcd');
    const importTool = document.querySelector('.import-tool-wpcd');

    importWPCDBtn.addEventListener('click', AnimationPlayer.importHelper);
    importTool.addEventListener('change', AnimationPlayer.importWPCD);

    window.addEventListener('keydown', AnimationPlayer.keyControl);
  }

  static keyControl({ code }) {
    const keyCode = code;
    switch (keyCode) {
      case ('KeyT'):
        AnimationPlayer.animationControl();
        break;

      case ('KeyG'):
        AnimationPlayer.fullScreen();
        break;

      case ('KeyQ'):
        AnimationPlayer.saveAsGif();
        break;

      case ('KeyW'):
        AnimationPlayer.saveAsGif();
        break;

      case ('KeyD'):
        AnimationPlayer.saveAsWPCD();
        break;

      default:
        break;
    }
  }

  static animationControl() {
    animPlayerData.startBtn.classList.toggle('pause');
    animateIsExist = !animateIsExist;
    if (!animateIsExist) {
      animateIsExist = false;
      return;
    }
    AnimationPlayer.animate();
  }

  static animate() {
    if (!animateIsExist) return;

    AnimationPlayer.getDelay();
    if (!_.isFinite(delay)) {
      animPlayerData.startBtn.classList.toggle('pause');
      animateIsExist = false;
      return;
    }

    index += 1;
    if (index >= canvasArr.length) index = 0;
    AnimationPlayer.drawNextImage(index);
    setTimeout(AnimationPlayer.animate, delay);
  }

  static formCanvasArr() {
    canvasArr = document.querySelectorAll('.frame-canvas');
  }

  static drawNextImage(number) {
    animPlayerData.animationContext.clearRect(0, 0, canvasData.canvasSize, canvasData.canvasSize);
    AnimationPlayer.formCanvasArr();

    if (_.isNull(canvasArr) || _.isUndefined(canvasArr[number])) return;

    animPlayerData.animationContext.drawImage(canvasArr[number], 0, 0);
  }

  static getDelay() {
    delay = Math.floor(1000 / animPlayerData.chooseFps.value);
  }

  static displayFps({ target }) {
    animPlayerData.fpsText.innerHTML = `FPS: ${target.value}`;
  }

  static fullScreen() {
    if (!document.fullscreen) {
      animPlayerData.animPlayer.requestFullscreen();
    } else {
      document.exitFullscreen();
    }
  }

  static saveAsWPCD() {
    const data = JSON.stringify(canvasData);
    const blob = new Blob([data], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const dataName = Math.random().toString(36).substr(2);
    const link = document.querySelector('.export-tool-wpcd-link');
    link.setAttribute('href', url);
    link.setAttribute('download', `${dataName}.wpcd`);
    link.click();
  }

  static importHelper() {
    const importTool = document.querySelector('.import-tool-wpcd');
    importTool.click();
  }

  static importWPCD({ target }) {
    const file = target.files[0];
    const reader = new FileReader();
    reader.onload = (event) => {
      const data = event.target.result;
      updateData(JSON.parse(data));
    };
    reader.readAsText(file);
  }

  static saveAsPng() {
    const url = canvasData.mainCanvas.toDataURL();
    const imageName = Math.random().toString(36).substr(2);

    const link = document.querySelector('.export-tool-png-link');
    link.setAttribute('href', url);
    link.setAttribute('download', `image_${imageName}`);
    link.click();
  }

  static async saveAsGif() {
    const x = delay;

    const path = await fetch('https://unpkg.com/gif.js@0.2/dist/gif.worker.js')
      .then(response => response.blob())
      .then(blob => URL.createObjectURL(blob, { type: 'text/javascript' }));

    const gif = new GIF({
      workers: 10,
      workerScript: path,
      quality: 10,
    });

    canvasArr.forEach(elem => gif.addFrame(elem, { delay: x }));

    gif.render();

    gif.on('finished', (blob) => {
      const url = URL.createObjectURL(blob);
      const link = document.querySelector('.export-tool-gif-link');
      const imageName = Math.random().toString(36).substr(2);
      link.setAttribute('href', url);
      link.setAttribute('download', `gif_${imageName}`);
      link.click();
    });
  }
}
